.input {
  box-sizing: border-box;
  font-family: 'pn-regular';
  width: 100%;
  height: 36px;
  border: 1px solid #344178;
  color: #344178;
  border-radius: 5px;
  padding-left: 15px;
  outline: none;

  &.input__error {
    border: 1px solid #d24b4b;
  }

  &.input__success {
    border: 1px solid #35ad35;
  }
}

.input-wrapper {
  position: relative;

  .input {
    padding-left: 40px;
  }

  &_login:before {
    background: url('../../../images/icons/login.png');
  }

  &_password:before {
    background: url('../../../images/icons/password.png');
  }

  &:before {
    position: absolute;
    content: '';
    left: 10px;
    top: 10px;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center;
  }
}